<template>
  <div class="btns">
    <div
      v-for="(button, index) in displayButtons"
      :key="button.id || index"
      class="button-wrapper"
    >
      <input
        type="button"
        :value="button.label"
        :disabled="button.disabled"
        @click="handleButtonClick(button.action)"
      />
      <span v-if="index < displayButtons.length - 1"
        >&nbsp;&nbsp;&nbsp;&nbsp;</span
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "GameControls",
  props: {
    // ‰ΩøÁî®Êï∞ÁªÑÂÆö‰πâÊåâÈíÆÈÖçÁΩÆÔºå‰ºòÂÖàÁ∫ßÈ´ò‰∫éÂçïÁã¨ÁöÑshowÂ±ûÊÄß
    buttons: {
      type: Array,
      default: () => [],
    },

    // ‰ª•‰∏ãÂ±ûÊÄß‰øùÊåÅÂêëÂêéÂÖºÂÆπÊÄß
    showUndo: {
      type: Boolean,
      default: true,
    },
    showRestart: {
      type: Boolean,
      default: true,
    },
    showStep: {
      type: Boolean,
      default: true,
    },
    showAuto: {
      type: Boolean,
      default: true,
    },

    undoDisabled: {
      type: Boolean,
      default: false,
    },
    restartDisabled: {
      type: Boolean,
      default: false,
    },
    stepDisabled: {
      type: Boolean,
      default: false,
    },
    autoDisabled: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    // Ê†πÊçÆbuttonsÊï∞ÁªÑÊàñÂçïÁã¨ÁöÑshowÂ±ûÊÄßÁîüÊàêË¶ÅÊòæÁ§∫ÁöÑÊåâÈíÆÂàóË°®
    displayButtons() {
      // Â¶ÇÊûúÊèê‰æõ‰∫ÜbuttonsÊï∞ÁªÑÔºåÂàô‰ΩøÁî®ÂÆÉ
      if (this.buttons && this.buttons.length > 0) {
        return this.buttons;
      }

      // Âê¶Âàô‰ΩøÁî®ÂçïÁã¨ÁöÑshowÂ±ûÊÄßÁîüÊàêÈªòËÆ§ÊåâÈíÆÂàóË°®Ôºà‰øùÊåÅÂêëÂêéÂÖºÂÆπÔºâ
      const defaultButtons = [];

      if (this.showUndo) {
        defaultButtons.push({
          label: "UNDO",
          action: "undo",
          disabled: this.undoDisabled,
        });
      }

      if (this.showRestart) {
        defaultButtons.push({
          label: "RESTART",
          action: "goon",
          disabled: this.restartDisabled,
        });
      }

      if (this.showStep) {
        defaultButtons.push({
          label: "STEP",
          action: "step",
          disabled: this.stepDisabled,
        });
      }

      if (this.showAuto) {
        defaultButtons.push({
          label: "AUTO",
          action: "auto",
          disabled: this.autoDisabled,
        });
      }

      return defaultButtons;
    },
  },
  methods: {
    // Áªü‰∏ÄÁöÑÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜ
    handleButtonClick(action) {
      this.$emit(action);
    },

    // ‰øùÁïôÂéüÊúâÊñπÊ≥ï‰ª•‰øùÊåÅÂÖºÂÆπÊÄß
    undo() {
      this.$emit("undo");
    },
    goon() {
      this.$emit("goon");
    },
    stepFn() {
      this.$emit("step");
    },
    pass() {
      this.$emit("auto");
    },
  },
};
</script>

