@import url("./sum.css");

/* ‰øùÊåÅ‰∏éÂéüÊ†∑Âºè‰∏ÄËá¥ÁöÑÊåâÈíÆÂÆπÂô®Ê†∑Âºè */
.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.625rem 0;
}

/* Ë¶ÜÁõñÂéüÊúâinputÊ†∑Âºè‰ª•Á°Æ‰øùÊåâÈíÆ‰∏ÄËá¥ÊÄß */
.btns input {
  margin: 1.25rem 0;
  color: #3c4245;
  padding: 0.3125rem 0.9375rem;
  border: 0.0625rem solid #dfcdc3;
