<template>
  <div class="game-layout-container">
    <!-- ÂàáÊç¢ÊåâÈíÆ -->
    <button
      class="toggle-header-btn"
      @click="toggleHeader"
      :title="isHeaderExpanded ? 'Êî∂Ëµ∑ÂØºËà™ÂíåÊ†áÈ¢ò' : 'Â±ïÂºÄÂØºËà™ÂíåÊ†áÈ¢ò'"
    >
      {{ isHeaderExpanded ? "‚ñ≤" : "‚ñº" }}
    </button>

    <!-- Fixed ÂØºËà™Ê†è -->
    <transition name="slide-down">
      <div v-show="isHeaderExpanded" ref="gameNav" class="game-nav">
        GAMES: &nbsp;
        <router-link to="/month">üåõ</router-link>
        &nbsp; / &nbsp;
        <router-link to="/fish">üêü</router-link>
        &nbsp; / &nbsp;
        <router-link to="/blackjack">‚ô†Ô∏è</router-link>
        &nbsp; / &nbsp;
        <router-link to="/point24">24</router-link>
        &nbsp; / &nbsp;
        <router-link to="/Tortoise">üê¢</router-link>
        &nbsp; / &nbsp;
        <router-link to="/Sort">üêó</router-link>
        &nbsp; / &nbsp;
        <router-link to="/Pairs">üê∞</router-link>
        &nbsp; / &nbsp;
        <router-link to="/Spider">üï∑Ô∏è</router-link>
        &nbsp; / &nbsp;
        <router-link to="/Chess">‚ôüÔ∏è</router-link>
      </div>
    </transition>

    <!-- Fixed Ê†áÈ¢òÂíåÈ°∂ÈÉ®ÊéßÂà∂Âå∫ -->
    <transition name="slide-down">
      <div
        v-show="isHeaderExpanded"
        ref="gameHeader"
        class="game-header"
        :style="{ top: navHeight / 16 + 'rem' }"
      >
        <h1>{{ title }}</h1>

        <!-- È°∂ÈÉ®ÊéßÂà∂ÊåâÈíÆÊèíÊßΩ -->
        <slot name="top-controls">
          <GameControls
            v-if="showTopControls"
            v-bind="gameControlsConfig"
            @undo="$emit('undo')"
            @goon="$emit('goon')"
            @step="$emit('step')"
            @auto="$emit('auto')"
          />
        </slot>
      </div>
    </transition>

    <!-- ÂèØÊªöÂä®ÁöÑÊ∏∏ÊàèÂÜÖÂÆπÂå∫Âüü -->
    <div class="game-content-wrapper" :style="contentWrapperStyle">
      <slot name="game-content"></slot>
    </div>

    <!-- Fixed Â∫ïÈÉ®ÊéßÂà∂ÊåâÈíÆ -->
    <div
      ref="gameFooter"
      class="game-footer"
      v-if="showBottomControls || $slots['bottom-controls']"
    >
      <slot name="bottom-controls">
        <GameControls
          v-if="showBottomControls"
          v-bind="gameControlsConfig"
          @undo="$emit('undo')"
          @goon="$emit('goon')"
          @step="$emit('step')"
          @auto="$emit('auto')"
        />
      </slot>
    </div>

    <!-- Ê∏∏ÊàèÁªìÊûúÊ®°ÊÄÅÊ°ÜÊèíÊßΩ -->
    <slot name="result-modals">
      <!-- Win Modal -->
      <GameResultModal
        v-if="winflag"
        :title="winTitle || 'U WIN!'"
        :subtitle="winSubtitle"
        :buttons="winButtons || defaultWinButtons"
        :modalStyle="winModalStyle"
        :customClass="winCustomClass"
      >
        <template v-if="$slots['win-content']" v-slot:content>
          <slot name="win-content"></slot>
        </template>
        <template v-if="$slots['win-cards']" v-slot:cards>
          <slot name="win-cards"></slot>
        </template>
      </GameResultModal>

      <!-- Lose Modal -->
      <GameResultModal
        v-if="loseflag"
        :title="loseTitle || 'U LOSE'"
        :subtitle="loseSubtitle"
        :buttons="loseButtons || defaultLoseButtons"
        :modalStyle="loseModalStyle || { backgroundColor: 'rgba(0,0,0,0.5)' }"
        :customClass="loseCustomClass"
      >
        <template v-if="$slots['lose-content']" v-slot:content>
          <slot name="lose-content"></slot>
        </template>
        <template v-if="$slots['lose-cards']" v-slot:cards>
          <slot name="lose-cards"></slot>
        </template>
      </GameResultModal>

      <!-- Draw Modal -->
      <GameResultModal
        v-if="drawflag"
        :title="drawTitle || 'DRAW GAME'"
        :subtitle="drawSubtitle"
        :buttons="drawButtons || defaultDrawButtons"
        :modalStyle="drawModalStyle || { backgroundColor: 'rgba(0,0,0,0.5)' }"
        :customClass="drawCustomClass"
      >
        <template v-if="$slots['draw-content']" v-slot:content>
          <slot name="draw-content"></slot>
        </template>
        <template v-if="$slots['draw-cards']" v-slot:cards>
          <slot name="draw-cards"></slot>
        </template>
      </GameResultModal>
    </slot>
  </div>
</template>

<script>
import GameControls from "./GameControls.vue";
import GameResultModal from "./GameResultModal.vue";

export default {
  name: "GameLayout",
  components: {
    GameControls,
    GameResultModal,
  },
  data() {
    return {
      isHeaderExpanded: true,
      navHeight: 0,
      headerHeight: 0,
      footerHeight: 0,
      resizeObserver: null,
    };
  },
  props: {
    // Âü∫Á°ÄÂ±ûÊÄß
    title: {
      type: String,
      default: "",
    },
    containerStyle: {
      type: Object,
      default: () => ({ width: "100%" }),
    },

    // ÊéßÂà∂ÊåâÈíÆÁõ∏ÂÖ≥
    showTopControls: {
      type: Boolean,
      default: false,
    },
    showBottomControls: {
      type: Boolean,
      default: false,
    },
    gameControlsConfig: {
      type: Object,
      default: () => ({}),
    },

    // Ê∏∏ÊàèÁä∂ÊÄÅÊ†áÂøó
    winflag: {
      type: Boolean,
      default: false,
    },
    loseflag: {
      type: Boolean,
      default: false,
    },
    drawflag: {
      type: Boolean,
      default: false,
    },

    // Win Modal ÈÖçÁΩÆ
    winTitle: String,
    winSubtitle: String,
    winButtons: Array,
    winModalStyle: Object,
    winCustomClass: String,

    // Lose Modal ÈÖçÁΩÆ
    loseTitle: String,
    loseSubtitle: String,
    loseButtons: Array,
    loseModalStyle: Object,
    loseCustomClass: String,

    // Draw Modal ÈÖçÁΩÆ
    drawTitle: String,
    drawSubtitle: String,
    drawButtons: Array,
    drawModalStyle: Object,
    drawCustomClass: String,

    // ÂÖ∂‰ªñÊ∏∏ÊàèÁõ∏ÂÖ≥Â±ûÊÄß
    step: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    contentWrapperStyle() {
      const topPadding = this.isHeaderExpanded
        ? this.navHeight + this.headerHeight + 20
        : 0;
      const bottomPadding = this.footerHeight + 20;

      return {
        ...this.containerStyle,
        paddingTop: `${topPadding / 16}rem`,
        paddingBottom: `${bottomPadding / 16}rem`,
      };
    },
    defaultWinButtons() {
      return [
        {
          text: "GO ON",
          callback: () => this.$emit("goon"),
          disabled: false,
        },
      ];
    },
    defaultLoseButtons() {
      return [
        {
          text: "RESTART",
          callback: () => this.$emit("goon"),
          disabled: false,
        },
        {
          text: "UNDO",
          callback: () => this.$emit("undo"),
          disabled: this.step <= 0,
        },
      ];
    },
    defaultDrawButtons() {
      return [
        {
          text: "RESTART",
          callback: () => this.$emit("goon"),
          disabled: false,
        },
        {
          text: "UNDO",
          callback: () => this.$emit("undo"),
          disabled: this.step <= 0,
        },
      ];
    },
  },
  mounted() {
    this.updateHeights();
    this.setupResizeObserver();
  },
  beforeUnmount() {
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
  },
  methods: {
    toggleHeader() {
      this.isHeaderExpanded = !this.isHeaderExpanded;
      this.$nextTick(() => {
        this.updateHeights();
      });
    },
    updateHeights() {
      this.$nextTick(() => {
        if (this.$refs.gameNav) {
          this.navHeight = this.$refs.gameNav.offsetHeight;
        }
        if (this.$refs.gameHeader) {
          this.headerHeight = this.$refs.gameHeader.offsetHeight;
        }
        if (this.$refs.gameFooter) {
          this.footerHeight = this.$refs.gameFooter.offsetHeight;
        }
      });
    },
    setupResizeObserver() {
      if (typeof ResizeObserver === "undefined") return;

      this.resizeObserver = new ResizeObserver(() => {
        this.updateHeights();
      });

      this.$nextTick(() => {
        if (this.$refs.gameNav) {
          this.resizeObserver.observe(this.$refs.gameNav);
        }
        if (this.$refs.gameHeader) {
          this.resizeObserver.observe(this.$refs.gameHeader);
        }
        if (this.$refs.gameFooter) {
          this.resizeObserver.observe(this.$refs.gameFooter);
        }
      });
    },
  },
  emits: ["undo", "goon", "step", "auto"],
};
</script>

